body {
  margin: 0;
  font-family:'AktivGrotesk', Helvetica,Arial,sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #16202b;
  color: #fff;
  text-align: center;
}
button {
  line-height: 1.5rem;
  text-align: center;
  text-transform: uppercase;
  text-decoration: none;
  padding: .688rem 1.5rem;
  margin: 1rem 0;
  width: 100%;
  font-weight: 300;
  background: #233143;
  color: #fff;
  border: none;
}

button:hover, button:disabled {
  background: #fff;
  color: #000;
}

input {
  box-sizing: border-box;
  color: #323232;
  font-size: 14px;
  height: 48px;
  font-family: 'Abel', Arial, Verdana, sans-serif;
  width: 91%;
  position: relative;
  top: 3px;
}

.sort:hover, .sort:disabled, .sort {
  background: #16202b;
  color: #fff;
  margin: 0;
  padding: 1.5rem 0 0 0;
  line-height: 1.5rem;
  text-transform: uppercase;
  text-decoration: none;
  font-weight: 300;
  font-size: 12px;
}